import { GAMESTATE } from "../constants";

function InfoWrapper({mines,leftMines,gameState,handleRestart,runtime}){
  
  const transformStatus=(state)=>{
    switch(state){
      case GAMESTATE.WIN: return "üòé";
      case GAMESTATE.LOSE: return "üò¢";
      default: return "üòÑ";
    }
  }

  let status=transformStatus(gameState);
  let runtimeStr=""+runtime;
  if(runtimeStr.length<3) runtimeStr="0".repeat(3-runtimeStr.length)+runtimeStr;

  return (
      <div className="info">
        <div className="count"><span>üí£</span>{leftMines}/{mines}</div>
        <div className="status"><button onClick={handleRestart}>{status}</button><button>‚öôÔ∏è</button></div>
        <div className="time"><span>üïô</span>{runtimeStr}</div>
      </div>
  );
    
}
export default InfoWrapper;